</template>

<script setup>
import {getCurrentInstance, onMounted, reactive} from "vue";
import {mainStore} from "@/pinia";
import sugar from './pages/sugar.vue'

// const fileList = reactive({
//   doc: require("@/assets/test/1.doc"),
//   docx: require("@/assets/test/2023年06月24日智慧服务工作记录.docx"),
//   pdf: require("@/assets/test/189049ca59c_4a3.pdf"),
//   xlsx: require("@/assets/test/合同列表(1).xlsx"),
// })

const store = mainStore()

const {proxy} = getCurrentInstance();

function getData() {
  //用setTimeout模拟网络请求延迟
  //Math.round(Math.random() * 5 + 1) 随机生成5-10的数字
  setTimeout(() => {
    proxy.$axios({
      url: '/getList',
      method: 'get',
      params: {
        type: 2
      }
    }).then((res) => {
      console.log('请求成功!', res)
    });
  }, Math.round(Math.random() * 5 + 5))
}

onMounted(() => {
  getData()
})
</script>

